<template>
  <div>
    <el-form :model="objForm" ref="objForm" label-width="150px">
      <el-form-item label="批量信息">
        <el-upload
          :action="uploadUrl"
          :on-success="uploadSuccess"
          :on-error="uploadError"
          :multiple="false"
          accept="csv"
          :limit="1"
          :on-exceed="uploadExceed"
          :file-list="fileList"
        >
          <el-button size="small" type="primary">点击上传</el-button>
          <div slot="tip" class="el-upload__tip">只能上传excel文件，且不超过500kb</div>
        </el-upload>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="submitFormMulti()">确定</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>
 
<script>
import baseURL from "../../config/baseUrl";
export default {
  name: "email-multi",
  data() {
    return {
      // 默认为没有上传成功或更新
      isUpdate: false,
      fileList: [],
      objForm: {
        fileId: null
      }
    };
  },
  computed: {
    uploadUrl() {
      return baseURL + API.upload;
    }
  },
  methods: {
    submitFormMulti() {
      if (this.objForm.fileId) {
        if (this.isUpdate) {
          let objCopy = JSON.parse(JSON.stringify(this.objForm));
          this.$fetch.post(API.gift.send_gift_batch, objCopy).then(response => {
            let res = response.data;
            if (res.status == 200) {
              this.fileList = [];
              this.isUpdate = false;
              this.$message.success(res.desc);
            } else {
              this.$message.error(res.desc);
            }
          });
        } else {
          this.$message.error("请重新上传文件！");
        }
      } else {
        this.$message.error("请上传文件！");
      }
    },

    uploadSuccess(response, file, fileList) {
      if (response.status == 200) {
        this.objForm.fileId = response.data.fileId;
        this.isUpdate = true;
        this.$message.success(response.desc);
      } else {
        this.$message.error(response.desc);
      }
    },
    uploadError(err, file, fileList) {
    },
    uploadExceed(files, fileList) {
      this.$message.error("文件上传数量超出上限！");
    }
  }
};
</script>
