<template>
  <div>
    <el-form :model="objForm" :rules="rules" ref="objForm" label-width="150px">
      <el-form-item label="福利" prop="giftId">
        <el-select v-model="objForm.giftId" placeholder="请选择福利">
          <el-option v-for="(ele,idx) in aGifts" :label="ele.name" :value="ele.giftId" :key="idx"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="角色ID" prop="pid">
        <el-input v-model="objForm.pid" placeholder="请输入角色ID"></el-input>
      </el-form-item>
      <el-form-item label="服务器ID" prop="serverId">
        <el-input v-model="objForm.serverId" placeholder="请输入服务器ID"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="submitForm('objForm')">确定</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
export default {
  name: "welfare-single",
  data() {
    return {
      aGifts: [],
      aReasons: [
        {
          id: "11",
          name: "充值"
        },
        {
          id: "22",
          name: "福利"
        }
      ],
      objForm: {},
      rules: {
        giftId: [
          {
            required: true,
            message: "请选择",
            trigger: "change"
          }
        ],
        pid: [
          {
            required: true,
            message: "请输入",
            trigger: "blur"
          }
        ],
        serverId: [
          {
            required: true,
            message: "请选择",
            trigger: "change"
          }
        ]
      }
    };
  },
  methods: {
    //   提交
    submitForm(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          let objCopy = JSON.parse(JSON.stringify(this.objForm));
          this.$fetch.post(API.gift.send_gift, objCopy).then(response => {
            let res = response.data;
            if (res.status == 200) {
              this.$message.success(res.desc);
              this.$refs[formName].resetFields();
            } else {
              this.$message.error(res.desc);
            }
          });
        } else {
          return false;
        }
      });
    },
    fetchGift() {
      this.$fetch.post(API.gift.gift_list).then(response => {
        let res = response.data;
        if (res.status == 200) {
          this.aGifts = res.data;
        } else {
          this.$message.error(res.desc);
        }
      });
    }
  },
  mounted() {
    this.fetchGift();
  }
};
</script>